import { Dialog, DialogContent, IconButton } from '@mui/material';
import { useEffect, useState } from 'react';
import CloseIcon from '@mui/icons-material/Close';

// Add a React import for useRef
import React from 'react';
import { autoJsStoreUtils, autoWebViewJs, isRealInAndroid } from '@droid/android';

interface PrivacyDialogProps {
  // open: boolean;
  // onClose: () => void;
  // onResult: (agree: boolean) => void;
  type: string;
  area: string;
}

interface IPrivacyInfo{
  agree:boolean;
  disagreeTime:number;
}

export function PrivacyDialog({ type, area }: PrivacyDialogProps) {
  const url = `/privacies/${type}/${area}.html`;
  const [iframeSrc, setIframeSrc] = useState(url);

  const [open, setOpen] = useState(false);
  function onClose() {
    setOpen(false);
  }

  useEffect(()=>{
    setIframeSrc(`/privacies/${type}/${area}.html`)
  }, [type, area]);

  useEffect(() => {
    const def:IPrivacyInfo = {agree:false, disagreeTime:0}
    const res = autoJsStoreUtils.read<IPrivacyInfo>(`privacy_${type}`, def);
    const value:IPrivacyInfo = res && 'agree' in res ? res : def;
    if(!isRealInAndroid){
      return;
    }
    if(value.agree){
      return
    }
    if(value.disagreeTime && value.disagreeTime != 0 &&  value.disagreeTime + 3 * 24 * 60 * 60 * 1000 > Date.now()){
      return;
    }
    setOpen(true)
  }, []);

  useEffect(() => {
    const handleMessage = (event: MessageEvent) => {
      // Basic security check: ensure the message is from a trusted source if possible
      // For now, we check the data structure.
      if (event.source !== iframeRef.current?.contentWindow) {
        return;
      }

      const { sender, agree } = event.data;

      if (sender === 'privacy_page') {
        console.log(`Received privacy decision: ${agree ? 'Agreed' : 'Disagreed'}`);

        const value:IPrivacyInfo = {
          agree,
          disagreeTime: agree ? 0 : Date.now()
        }
        autoJsStoreUtils.write(`privacy_${type}`, value);
        const s = `
        var data = new com.fanfanlo.emergencycall.data.PrivacyData(${value.agree}, ${value.disagreeTime});
        com.fanfanlo.emergencycall.data.PrivacyStateHolder.updateMainPrivacyByJs(data);
        `
        autoWebViewJs.callScript(s);

        // onResult(agree);
        onClose();
      }
    };

    window.addEventListener('message', handleMessage);

    return () => {
      window.removeEventListener('message', handleMessage);
    };
  }, []);

  const iframeRef = React.useRef<HTMLIFrameElement>(null);

  return (
    <Dialog
      open={open}
      onClose={onClose}
      PaperProps={{
        sx: {
          width: '80%',
          height: '80%',
          maxWidth: 'none',
        },
      }}>
      <IconButton
        aria-label="close"
        onClick={onClose}
        sx={{
          position: 'absolute',
          right: 8,
          top: 8,
          color: theme => theme.palette.grey[500],
        }}>
        <CloseIcon />
      </IconButton>
      <DialogContent sx={{ padding: 0, overflow: 'hidden' }}>
        {iframeSrc && (
          <iframe
            ref={iframeRef}
            src={iframeSrc}
            style={{ width: '100%', height: '100%', border: 'none' }}
            title={`Privacy Policy - ${type}/${area}`}
          />
        )}
      </DialogContent>
    </Dialog>
  );
}


