import { request } from "@fanfanlo";
import { tms } from "../tms";

// 权限状态信息
export interface PermissionStatusInfo {
  user_id: string;
  user_name?: string;
  device_id: string;
  camera_status?: string | null;
  microphone_status?: string | null;
  location_status?: string | null;
  overlay_status?: string | null;
  battery_optimization_status?: string | null;
  notification_status?: string | null;
  screen_on_status?: string | null;
  overall_status: string;
  last_check_time?: string | null;
  last_client_timestamp?: number | null;
}

// 获取相关权限参数
export interface IParamsGetRelatedPermissions {
  page_size?: number;
  page_offset?: number;
  include_myself?: boolean;
  include_contacts?: boolean;
}

// 获取相关权限返回结果
export interface IResultGetRelatedPermissions {
  is_ok: boolean;
  msg: string;
  data?: {
    permissions: PermissionStatusInfo[];
    total_count: number;
    page_info: {
      current_page: number;
      page_size: number;
      total_pages: number;
    };
  };
}

// 获取相关用户权限状态 API 客户端
export async function getRelatedPermissions(
  params: IParamsGetRelatedPermissions = {}
): Promise<IResultGetRelatedPermissions> {
  const defaultParams = {
    page_size: 50,
    page_offset: 0,
    include_myself: true,
    include_contacts: true,
    ...params,
  };

  return request({
    t: tms.TPermission,
    m: tms.MPermissionGetRelatedPermissions,
    p: defaultParams,
  });
}