import React, { useState, useEffect } from 'react';
import {
  Box,
  TextField,
  Button,
  Typography,
  Paper,
  Dialog,
  DialogActions,
  DialogContent,
  DialogContentText,
  DialogTitle,
  Switch,
  FormControlLabel,
  Chip,
  Divider,
  Alert,
  Grid2,
  Accordion,
  AccordionSummary,
  AccordionDetails,
  List,
  ListItem,
  ListItemIcon,
  ListItemText,
  CircularProgress,
} from '@mui/material';
import {
  Sensors,
  Security,
  PlayArrow,
  Stop,
  Settings as SettingsIcon,
  ExpandMore,
  CheckCircle,
  Cancel,
  Help,
  Person,
  Groups,
} from '@mui/icons-material';
import { autoJsStoreUtils, autoWebViewJs } from '@droid/android';
import { getRelatedPermissions, type PermissionStatusInfo } from '@app/model';

// 监控状态接口
interface MonitoringState {
  sensorEnabled: boolean;
  permissionEnabled: boolean;
  isRunning: boolean;
  threshold: string;
}

// 权限检查接口
interface ForegroundServiceInfo {
  passed: boolean;
}

// Android权限状态接口
interface AndroidPermissionStatus {
  overall_status: string;
  permissions: Array<{
    permission_type: string;
    level: string;
    can_detect: boolean;
    description: string;
    timestamp: number;
  }>;
  last_updated: number;
}

export const UnifiedMonitoringControl: React.FC = () => {
  const [state, setState] = useState<MonitoringState>({
    sensorEnabled: false,
    permissionEnabled: false,
    isRunning: false,
    threshold: '5.0',
  });
  
  const [dialogOpen, setDialogOpen] = useState(false);
  const [dialogCallback, setDialogCallback] = useState<{
    onPassed: () => void;
    onCancel: () => void;
  } | null>(null);

  // 权限状态相关state
  const [permissionExpanded, setPermissionExpanded] = useState(false);
  const [keepExpanded, setKeepExpanded] = useState(false);
  const [permissionData, setPermissionData] = useState<PermissionStatusInfo[]>([]);
  const [selfPermissionData, setSelfPermissionData] = useState<AndroidPermissionStatus | null>(null);
  const [permissionLoading, setPermissionLoading] = useState(false);
  const [lastBackendUpdate, setLastBackendUpdate] = useState<number>(0);
  const [lastSelfUpdate, setLastSelfUpdate] = useState<number>(0);

  // 初始化：获取Android端状态
  useEffect(() => {
    loadInitialState();
  }, []);

  // 权限数据更新定时器
  useEffect(() => {
    let backendInterval: NodeJS.Timeout;
    let selfInterval: NodeJS.Timeout;

    if (permissionExpanded || keepExpanded) {
      // 后端API每5分钟更新一次
      backendInterval = setInterval(() => {
        loadBackendPermissions();
      }, 5 * 60 * 1000);

      // 自己的权限每1分钟更新一次
      selfInterval = setInterval(() => {
        loadSelfPermissions();
      }, 60 * 1000);

      // 立即加载一次
      loadBackendPermissions();
      loadSelfPermissions();
    }

    return () => {
      if (backendInterval) clearInterval(backendInterval);
      if (selfInterval) clearInterval(selfInterval);
    };
  }, [permissionExpanded, keepExpanded, state.permissionEnabled]);

  const loadInitialState = async () => {
    try {
      // 获取传感器监控状态
      const sensorScript = `
        var isMonitoring = com.fanfanlo.emergencycall.manager.SensorManager.isMonitoring();
        isMonitoring;
      `;
      const { javaResultString: sensorResult } = autoWebViewJs.callScript(sensorScript);
      const isRunning = sensorResult === 'true';

      // 获取震动阈值
      const thresholdScript = `
        var thresholdValue = com.fanfanlo.emergencycall.manager.SensorManager.getThreshold();
        thresholdValue;
      `;
      const { javaResultString: thresholdResult } = autoWebViewJs.callScript(thresholdScript);
      const thresholdValue = parseFloat(thresholdResult || '5.0');

      // 获取权限监控状态 (从即将扩展的SensorManager中获取)
      const permissionScript = `
        var isPermissionMonitoring = com.fanfanlo.emergencycall.manager.SensorManager.isPermissionMonitoringEnabled();
        isPermissionMonitoring;
      `;
      let permissionEnabled = false;
      try {
        const { javaResultString: permissionResult } = autoWebViewJs.callScript(permissionScript);
        permissionEnabled = permissionResult === 'true';
      } catch (error) {
        console.log('Permission monitoring not yet available:', error);
      }

      setState(prev => ({
        ...prev,
        isRunning,
        threshold: thresholdValue.toString(),
        sensorEnabled: isRunning, // 如果服务在运行，假设传感器已启用
        permissionEnabled,
      }));

      console.log('Initial state loaded:', {
        isRunning,
        threshold: thresholdValue,
        permissionEnabled
      });
    } catch (error) {
      console.error('Error loading initial state:', error);
    }
  };

  // 加载后端权限数据
  const loadBackendPermissions = async () => {
    if (!state.permissionEnabled) return;
    
    try {
      setPermissionLoading(true);
      const result = await getRelatedPermissions({
        include_myself: false,
        include_contacts: true,
        page_size: 50
      });
      
      if (result.is_ok && result.data) {
        setPermissionData(result.data.permissions);
        setLastBackendUpdate(Date.now());
      }
    } catch (error) {
      console.error('加载后端权限数据失败:', error);
    } finally {
      setPermissionLoading(false);
    }
  };

  // 加载自己的权限数据
  const loadSelfPermissions = async () => {
    if (!state.permissionEnabled) return;
    
    try {
      const script = `com.fanfanlo.emergencycall.permission.PermissionManager.getCurrentPermissionStatusJson();`;
      const { javaResultString } = autoWebViewJs.callScript(script);
      
      if (javaResultString) {
        const data = JSON.parse(javaResultString) as AndroidPermissionStatus;
        setSelfPermissionData(data);
        setLastSelfUpdate(Date.now());
      }
    } catch (error) {
      console.error('加载自己权限数据失败:', error);
    }
  };

  // 权限状态图标
  const getPermissionIcon = (status: string) => {
    switch (status?.toLowerCase()) {
      case 'granted':
      case 'high':
        return <CheckCircle color="success" />;
      case 'denied':
      case 'low':
        return <Cancel color="error" />;
      case 'medium':
        return <Help color="warning" />;
      default:
        return <Help color="disabled" />;
    }
  };

  // 格式化权限类型显示名称
  const formatPermissionType = (type: string) => {
    const typeMap: { [key: string]: string } = {
      'camera': '相机权限',
      'microphone': '麦克风权限',
      'location': '位置权限',
      'overlay': '悬浮窗权限',
      'battery_optimization': '电池优化',
      'notification': '通知权限',
      'screen_on': '屏幕常亮'
    };
    return typeMap[type] || type;
  };

  // 渲染权限列表项
  const renderPermissionItem = (permission: any, isAndroidData = false) => {
    const status = isAndroidData ? permission.level : (
      permission.camera_status || 
      permission.microphone_status || 
      permission.location_status || 
      permission.overlay_status || 
      permission.battery_optimization_status || 
      permission.notification_status || 
      permission.screen_on_status ||
      permission.overall_status
    );
    
    return (
      <ListItem key={isAndroidData ? permission.permission_type : permission.user_id}>
        <ListItemIcon>
          {getPermissionIcon(status)}
        </ListItemIcon>
        <ListItemText
          primary={
            isAndroidData 
              ? formatPermissionType(permission.permission_type)
              : (permission.user_name || `用户 ${permission.user_id}`)
          }
          secondary={
            isAndroidData
              ? `状态: ${status} | ${permission.description}`
              : `总体状态: ${permission.overall_status} | 最后检查: ${permission.last_check_time ? new Date(permission.last_check_time).toLocaleString() : '未知'}`
          }
        />
      </ListItem>
    );
  };

  const handleSetThreshold = () => {
    const thresholdValue = parseFloat(state.threshold);
    if (isNaN(thresholdValue)) {
      alert('请输入一个有效的数字作为阈值。');
      return;
    }
    
    try {
      const script = `com.fanfanlo.emergencycall.manager.SensorManager.updateThreshold(${thresholdValue});`;
      autoWebViewJs.callScript(script);
      alert(`震动阈值已设置为: ${thresholdValue}`);
    } catch (error) {
      console.error('Error setting threshold:', error);
      alert('设置阈值失败。请确保您在安卓应用中。');
    }
  };

  const handleToggleMonitoring = () => {
    if (state.isRunning) {
      // 停止监控服务
      stopMonitoring();
    } else {
      // 检查是否至少启用一种监控
      if (!state.sensorEnabled && !state.permissionEnabled) {
        alert('请至少启用一种监控功能（传感器监控或权限监控）');
        return;
      }
      
      // 检查前台服务权限，然后启动
      checkForegroundServicePermission(() => {
        startMonitoring();
      });
    }
  };

  const startMonitoring = () => {
    try {
      // 调用统一的启动方法
      const script = `
        com.fanfanlo.emergencycall.manager.SensorManager.startUnifiedMonitoring(${state.sensorEnabled}, ${state.permissionEnabled});
      `;
      autoWebViewJs.callScript(script);
      
      setState(prev => ({ ...prev, isRunning: true }));
      console.log('Started unified monitoring:', { 
        sensor: state.sensorEnabled, 
        permission: state.permissionEnabled 
      });
    } catch (error) {
      console.error('Error starting monitoring:', error);
      alert('启动监控失败。');
    }
  };

  const stopMonitoring = () => {
    try {
      const script = `com.fanfanlo.emergencycall.manager.SensorManager.stopMonitoring();`;
      autoWebViewJs.callScript(script);
      
      setState(prev => ({ ...prev, isRunning: false }));
      console.log('Stopped monitoring');
    } catch (error) {
      console.error('Error stopping monitoring:', error);
      alert('停止监控失败。');
    }
  };

  const checkForegroundServicePermission = (callback: () => void) => {
    const info = autoJsStoreUtils.read<ForegroundServiceInfo>('showForegroundServiceInfo', { passed: false });
    
    if (!info || !info.passed) {
      setDialogCallback({
        onPassed: () => {
          autoJsStoreUtils.write('showForegroundServiceInfo', { passed: true });
          setDialogOpen(false);
          callback();
        },
        onCancel: () => {
          autoJsStoreUtils.write('showForegroundServiceInfo', { passed: false });
          setDialogOpen(false);
        }
      });
      setDialogOpen(true);
    } else {
      callback();
    }
  };

  const handleSensorToggle = (enabled: boolean) => {
    setState(prev => ({ ...prev, sensorEnabled: enabled }));
  };

  const handlePermissionToggle = (enabled: boolean) => {
    setState(prev => ({ ...prev, permissionEnabled: enabled }));
    
    // 如果启用权限监控，同时更新Android端配置
    if (enabled) {
      try {
        const script = `com.fanfanlo.emergencycall.manager.SensorManager.enablePermissionMonitoring(true);`;
        autoWebViewJs.callScript(script);
      } catch (error) {
        console.error('Error enabling permission monitoring:', error);
      }
    }
  };

  const getStatusChips = () => {
    const chips = [];
    
    if (state.sensorEnabled) {
      chips.push(
        <Chip 
          key="sensor" 
          icon={<Sensors />} 
          label="传感器监控" 
          color="primary" 
          size="small" 
        />
      );
    }
    
    if (state.permissionEnabled) {
      chips.push(
        <Chip 
          key="permission" 
          icon={<Security />} 
          label="权限监控" 
          color="secondary" 
          size="small" 
        />
      );
    }

    return chips;
  };

  return (
    <Paper elevation={3} sx={{ padding: 2, margin: 1 }}>
      <Typography variant="h6" gutterBottom sx={{ display: 'flex', alignItems: 'center', gap: 1 }}>
        <SettingsIcon />
        统一监控控制
      </Typography>

      {/* 状态显示 */}
      <Box sx={{ mb: 2 }}>
        <Typography variant="body2" color="text.secondary" sx={{ mb: 1 }}>
          当前状态: {state.isRunning ? '监控中' : '已停止'}
        </Typography>
        <Box sx={{ display: 'flex', gap: 1, flexWrap: 'wrap' }}>
          {getStatusChips()}
          {!state.sensorEnabled && !state.permissionEnabled && (
            <Chip label="未选择监控功能" color="default" size="small" />
          )}
        </Box>
      </Box>

      <Divider sx={{ my: 2 }} />

      {/* 传感器监控设置 */}
      <Box sx={{ mb: 2 }}>
        <FormControlLabel
          control={
            <Switch
              checked={state.sensorEnabled}
              onChange={(e) => handleSensorToggle(e.target.checked)}
              disabled={state.isRunning}
            />
          }
          label={
            <Box>
              <Typography variant="subtitle2">
                <Sensors sx={{ verticalAlign: 'bottom', mr: 0.5 }} />
                传感器监控 (摔倒检测)
              </Typography>
              <Typography variant="body2" color="text.secondary">
                监控设备加速度和陀螺仪数据，检测摔倒事件
              </Typography>
            </Box>
          }
        />

        {state.sensorEnabled && (
          <Box sx={{ ml: 4, mt: 1, display: 'flex', alignItems: 'center', gap: 2 }}>
            <TextField
              label="震动阈值"
              variant="outlined"
              type="number"
              value={state.threshold}
              onChange={(e) => setState(prev => ({ ...prev, threshold: e.target.value }))}
              size="small"
              sx={{ width: 120 }}
              disabled={state.isRunning}
            />
            <Button 
              variant="outlined" 
              onClick={handleSetThreshold}
              size="small"
              disabled={state.isRunning}
            >
              设置
            </Button>
          </Box>
        )}
      </Box>

      {/* 权限监控设置 */}
      <Box sx={{ mb: 2 }}>
        <FormControlLabel
          control={
            <Switch
              checked={state.permissionEnabled}
              onChange={(e) => handlePermissionToggle(e.target.checked)}
              disabled={state.isRunning}
            />
          }
          label={
            <Box>
              <Typography variant="subtitle2">
                <Security sx={{ verticalAlign: 'bottom', mr: 0.5 }} />
                权限监控
              </Typography>
              <Typography variant="body2" color="text.secondary">
                定期检测关键权限状态，确保紧急功能可用
              </Typography>
            </Box>
          }
        />
      </Box>

      <Divider sx={{ my: 2 }} />

      {/* 权限状态显示 */}
      {state.permissionEnabled && (
        <Accordion 
          expanded={permissionExpanded} 
          onChange={(_, expanded) => setPermissionExpanded(expanded)}
          sx={{ mb: 2 }}
        >
          <AccordionSummary expandIcon={<ExpandMore />}>
            <Box sx={{ display: 'flex', alignItems: 'center', gap: 1, width: '100%' }}>
              <Groups />
              <Typography variant="subtitle2">权限状态监控</Typography>
              {permissionLoading && <CircularProgress size={16} />}
              <Box sx={{ ml: 'auto' }}>
                <FormControlLabel
                  control={
                    <Switch
                      checked={keepExpanded}
                      onChange={(e) => setKeepExpanded(e.target.checked)}
                      size="small"
                    />
                  }
                  label="保持展开"
                  onClick={(e) => e.stopPropagation()}
                />
              </Box>
            </Box>
          </AccordionSummary>
          <AccordionDetails>
            <Box>
              {/* 自己的权限状态 */}
              {selfPermissionData && (
                <Box sx={{ mb: 2 }}>
                  <Typography variant="subtitle2" sx={{ display: 'flex', alignItems: 'center', gap: 1, mb: 1 }}>
                    <Person />
                    我的权限状态
                    <Chip 
                      label={`总体: ${selfPermissionData.overall_status}`} 
                      size="small" 
                      color={selfPermissionData.overall_status === 'high' ? 'success' : selfPermissionData.overall_status === 'medium' ? 'warning' : 'error'}
                    />
                  </Typography>
                  <List dense>
                    {selfPermissionData.permissions.map(permission => 
                      renderPermissionItem(permission, true)
                    )}
                  </List>
                  <Typography variant="caption" color="text.secondary">
                    最后更新: {lastSelfUpdate ? new Date(lastSelfUpdate).toLocaleString() : '未更新'} (每分钟刷新)
                  </Typography>
                </Box>
              )}

              {/* 其他用户权限状态 */}
              {permissionData.length > 0 && (
                <Box>
                  <Typography variant="subtitle2" sx={{ display: 'flex', alignItems: 'center', gap: 1, mb: 1 }}>
                    <Groups />
                    联系人权限状态
                  </Typography>
                  <List dense>
                    {permissionData.map(permission => 
                      renderPermissionItem(permission, false)
                    )}
                  </List>
                  <Typography variant="caption" color="text.secondary">
                    最后更新: {lastBackendUpdate ? new Date(lastBackendUpdate).toLocaleString() : '未更新'} (每5分钟刷新)
                  </Typography>
                </Box>
              )}

              {permissionData.length === 0 && !selfPermissionData && !permissionLoading && (
                <Alert severity="info">
                  暂无权限数据。请确保权限监控已启用且有联系人数据。
                </Alert>
              )}
            </Box>
          </AccordionDetails>
        </Accordion>
      )}

      <Divider sx={{ my: 2 }} />

      {/* 控制按钮 */}
      <Grid2 container spacing={2}>
        <Grid2 size={12}>
          <Button
            variant="contained"
            color={state.isRunning ? 'error' : 'primary'}
            onClick={handleToggleMonitoring}
            fullWidth
            startIcon={state.isRunning ? <Stop /> : <PlayArrow />}
            disabled={!state.isRunning && !state.sensorEnabled && !state.permissionEnabled}
          >
            {state.isRunning ? '停止监控' : '开始监控'}
          </Button>
        </Grid2>
      </Grid2>

      {/* 提示信息 */}
      {!state.sensorEnabled && !state.permissionEnabled && (
        <Alert severity="info" sx={{ mt: 2 }}>
          请至少选择一种监控功能：传感器监控用于摔倒检测，权限监控确保紧急功能可用。
        </Alert>
      )}

      {/* 前台服务授权对话框 */}
      <Dialog
        open={dialogOpen}
        onClose={() => dialogCallback?.onCancel()}
        aria-labelledby="foreground-service-dialog-title"
      >
        <DialogTitle id="foreground-service-dialog-title">
          启用前台服务通知
        </DialogTitle>
        <DialogContent>
          <DialogContentText>
            为了在应用退到后台后仍能持续监控传感器数据和权限状态，我们需要启用一个前台服务。
            这会在您的设备通知栏显示一个持续的通知。您是否同意？
          </DialogContentText>
          
          <Box sx={{ mt: 2 }}>
            <Typography variant="body2" color="text.secondary">
              前台服务将监控以下功能：
            </Typography>
            <Box sx={{ mt: 1 }}>
              {state.sensorEnabled && (
                <Chip icon={<Sensors />} label="传感器监控" size="small" sx={{ mr: 1, mb: 1 }} />
              )}
              {state.permissionEnabled && (
                <Chip icon={<Security />} label="权限监控" size="small" sx={{ mr: 1, mb: 1 }} />
              )}
            </Box>
          </Box>
        </DialogContent>
        <DialogActions>
          <Button onClick={() => dialogCallback?.onCancel()}>
            不同意
          </Button>
          <Button onClick={() => dialogCallback?.onPassed()} autoFocus variant="contained">
            同意
          </Button>
        </DialogActions>
      </Dialog>
    </Paper>
  );
};

export default UnifiedMonitoringControl;