import React, { useState, useEffect } from 'react';
import {
  Box,
  Typography,
  Switch,
  TextField,
  Paper,
  Button,
  FormControlLabel,
  Slider,
  Divider,
  Alert,
  Snackbar,
} from '@mui/material';
import { PlayArrow, Stop, VolumeUp } from '@mui/icons-material';
import { autoJsStoreUtils, autoWebViewJs } from '@droid/android';

// TTS设置数据结构
interface EmergencyTTSSettings {
  enabled: boolean;
  message: string;
  repeatInterval: number;
  voiceSpeed: number;
  voicePitch: number;
  lastUpdated: number;
}

// 默认设置
const DEFAULT_SETTINGS: EmergencyTTSSettings = {
  enabled: false,
  message: '我遇到了紧急情况，需要帮助！请立即联系紧急服务！',
  repeatInterval: 3000,
  voiceSpeed: 1.0,
  voicePitch: 1.0,
  lastUpdated: Date.now(),
};

const TTS_SETTINGS_KEY = 'emergencyTTSSettings';

export const EmergencyTTSSettings: React.FC = () => {
  const [settings, setSettings] = useState<EmergencyTTSSettings>(DEFAULT_SETTINGS);
  const [loading, setLoading] = useState(true);
  const [testing, setTesting] = useState(false);
  const [showSuccess, setShowSuccess] = useState(false);
  const [error, setError] = useState<string | null>(null);

  // 加载设置
  useEffect(() => {
    const loadSettings = async () => {
      try {
        setLoading(true);
        const savedSettings = await autoJsStoreUtils.read<EmergencyTTSSettings>(
          TTS_SETTINGS_KEY, 
          DEFAULT_SETTINGS
        );
        setSettings(savedSettings);
      } catch (err) {
        console.error('加载TTS设置失败:', err);
        setError('加载设置失败');
      } finally {
        setLoading(false);
      }
    };

    loadSettings();
  }, []);

  // 保存设置
  const saveSettings = async (newSettings: EmergencyTTSSettings) => {
    try {
      const settingsToSave = {
        ...newSettings,
        lastUpdated: Date.now(),
      };
      
      await autoJsStoreUtils.write(TTS_SETTINGS_KEY, settingsToSave);
      setSettings(settingsToSave);
      setShowSuccess(true);
      setError(null);
    } catch (err) {
      console.error('保存TTS设置失败:', err);
      setError('保存设置失败');
    }
  };

  // 测试TTS
  const testTTS = async () => {
    if (!settings.message.trim()) {
      setError('请输入呼救文字后再测试');
      return;
    }

    try {
      setTesting(true);
      
      // 调用Android端TTS测试方法
      const script = `
        com.fanfanlo.emergencycall.manager.EmergencyTTSManager.testTTS("${settings.message}", ${settings.voiceSpeed}, ${settings.voicePitch});
      `;
      
      await new Promise(resolve => setTimeout(resolve, 500)); // 模拟测试时间
      // TODO: 实际调用时取消注释
      autoWebViewJs.callScript(script);
      
    } catch (err) {
      console.error('TTS测试失败:', err);
      setError('TTS测试失败');
    } finally {
      setTesting(false);
    }
  };

  // 停止TTS测试
  const stopTest = async () => {
    try {
      const script = `
        com.fanfanlo.emergencycall.manager.EmergencyTTSManager.stopTest();
      `;
      // TODO: 实际调用时取消注释
      // autoWebViewJs.callScript(script);
      setTesting(false);
    } catch (err) {
      console.error('停止TTS测试失败:', err);
    }
  };

  const handleSettingChange = (key: keyof EmergencyTTSSettings, value: any) => {
    const newSettings = { ...settings, [key]: value };
    setSettings(newSettings);
  };

  const handleSave = () => {
    if (settings.enabled && !settings.message.trim()) {
      setError('启用TTS功能时，呼救文字不能为空');
      return;
    }
    saveSettings(settings);
  };

  if (loading) {
    return (
      <Box sx={{ p: 2, textAlign: 'center' }}>
        <Typography>加载中...</Typography>
      </Box>
    );
  }

  return (
    <Box sx={{ p: 2, maxWidth: 600, margin: '0 auto' }}>
      <Typography variant="h5" sx={{ mb: 3, fontWeight: 'bold' }}>
        <VolumeUp sx={{ mr: 1, verticalAlign: 'bottom' }} />
        紧急呼救语音设置
      </Typography>

      <Paper sx={{ p: 3, mb: 2 }}>
        {/* 启用开关 */}
        <FormControlLabel
          control={
            <Switch
              checked={settings.enabled}
              onChange={(e) => handleSettingChange('enabled', e.target.checked)}
              color="primary"
            />
          }
          label={
            <Box>
              <Typography variant="subtitle1" fontWeight="bold">
                启用TTS语音呼救
              </Typography>
              <Typography variant="body2" color="text.secondary">
                开启后将使用TTS语音代替默认的呼救音频
              </Typography>
            </Box>
          }
        />

        <Divider sx={{ my: 3 }} />

        {/* 呼救文字设置 */}
        <Box sx={{ mb: 3 }}>
          <Typography variant="subtitle1" fontWeight="bold" sx={{ mb: 2 }}>
            呼救文字内容
          </Typography>
          <TextField
            fullWidth
            multiline
            rows={3}
            value={settings.message}
            onChange={(e) => handleSettingChange('message', e.target.value)}
            placeholder="请输入紧急情况下要播报的文字..."
            disabled={!settings.enabled}
            helperText={`当前字数: ${settings.message.length} 字符`}
            sx={{ 
              '& .MuiInputBase-input': {
                fontSize: '16px',
                lineHeight: 1.5,
              }
            }}
          />
        </Box>

        {/* 语音设置 */}
        {settings.enabled && (
          <>
            <Divider sx={{ my: 3 }} />
            
            <Typography variant="subtitle1" fontWeight="bold" sx={{ mb: 2 }}>
              语音参数设置
            </Typography>

            {/* 重复间隔 */}
            <Box sx={{ mb: 3 }}>
              <Typography variant="body2" sx={{ mb: 1 }}>
                重复间隔: {settings.repeatInterval / 1000} 秒
              </Typography>
              <Slider
                value={settings.repeatInterval}
                onChange={(_, value) => handleSettingChange('repeatInterval', value)}
                min={2000}
                max={10000}
                step={500}
                marks={[
                  { value: 2000, label: '2s' },
                  { value: 5000, label: '5s' },
                  { value: 10000, label: '10s' },
                ]}
                valueLabelDisplay="auto"
                valueLabelFormat={(value) => `${value / 1000}s`}
              />
            </Box>

            {/* 语音速度 */}
            <Box sx={{ mb: 3 }}>
              <Typography variant="body2" sx={{ mb: 1 }}>
                语音速度: {settings.voiceSpeed}x
              </Typography>
              <Slider
                value={settings.voiceSpeed}
                onChange={(_, value) => handleSettingChange('voiceSpeed', value)}
                min={0.5}
                max={2.0}
                step={0.1}
                marks={[
                  { value: 0.5, label: '0.5x' },
                  { value: 1.0, label: '1.0x' },
                  { value: 2.0, label: '2.0x' },
                ]}
                valueLabelDisplay="auto"
              />
            </Box>

            {/* 语音音调 */}
            <Box sx={{ mb: 3 }}>
              <Typography variant="body2" sx={{ mb: 1 }}>
                语音音调: {settings.voicePitch}x
              </Typography>
              <Slider
                value={settings.voicePitch}
                onChange={(_, value) => handleSettingChange('voicePitch', value)}
                min={0.5}
                max={2.0}
                step={0.1}
                marks={[
                  { value: 0.5, label: '低' },
                  { value: 1.0, label: '标准' },
                  { value: 2.0, label: '高' },
                ]}
                valueLabelDisplay="auto"
              />
            </Box>
          </>
        )}

        <Divider sx={{ my: 3 }} />

        {/* 操作按钮 */}
        <Box sx={{ display: 'flex', gap: 2, flexWrap: 'wrap' }}>
          <Button
            variant="contained"
            onClick={handleSave}
            sx={{ minWidth: 100 }}
          >
            保存设置
          </Button>

          {settings.enabled && settings.message.trim() && (
            <>
              {!testing ? (
                <Button
                  variant="outlined"
                  startIcon={<PlayArrow />}
                  onClick={testTTS}
                  sx={{ minWidth: 100 }}
                >
                  测试语音
                </Button>
              ) : (
                <Button
                  variant="outlined"
                  color="error"
                  startIcon={<Stop />}
                  onClick={stopTest}
                  sx={{ minWidth: 100 }}
                >
                  停止测试
                </Button>
              )}
            </>
          )}
        </Box>

        {/* 使用说明 */}
        <Box sx={{ mt: 3, p: 2, backgroundColor: 'rgba(0,0,0,0.05)', borderRadius: 1 }}>
          <Typography variant="body2" color="text.secondary">
            <strong>使用说明:</strong><br />
            • 启用后，紧急呼救将使用TTS语音代替默认音频<br />
            • 请设置简洁明了的呼救文字，确保在紧急情况下能清楚传达<br />
            • 建议先测试语音效果，确认语速和音调适中<br />
            • 重复间隔设置过短可能影响语音清晰度
          </Typography>
        </Box>
      </Paper>

      {/* 成功提示 */}
      <Snackbar
        open={showSuccess}
        autoHideDuration={3000}
        onClose={() => setShowSuccess(false)}
        anchorOrigin={{ vertical: 'bottom', horizontal: 'center' }}
      >
        <Alert onClose={() => setShowSuccess(false)} severity="success">
          TTS设置保存成功！
        </Alert>
      </Snackbar>

      {/* 错误提示 */}
      <Snackbar
        open={!!error}
        autoHideDuration={6000}
        onClose={() => setError(null)}
        anchorOrigin={{ vertical: 'bottom', horizontal: 'center' }}
      >
        <Alert onClose={() => setError(null)} severity="error">
          {error}
        </Alert>
      </Snackbar>
    </Box>
  );
};